<template>
  <div class="sample-list">
    <el-card>
      <div slot="header">
        <span class="card-name">样本库</span>
        <router-link class="el-button el-button--primary" to="permission-setting">预警权限设置</router-link>
      </div>
      <div class="frame-fun clear">
        <span class="total-samples">共 {{total}} 个样本</span>
        <router-link class="to-import" :to="{name:'importexcel'}">
          <i class="el-icon-download"></i> Excel导入
        </router-link>
        <el-input placeholder="请输入" prefix-icon="el-icon-search" v-model="moduleName">
          <el-button slot="append" @click.stop="getDatasource(moduleName)" type="primary">搜索</el-button>
        </el-input>
      </div>
      <el-table :data="dataSource" stripe style="width: 100%" :header-row-style="headerStyle">
        <el-table-column type="index" label="序号" align="center" width="50"></el-table-column>
        <el-table-column prop="modulename" label="样本名称" align="center" show-overflow-tooltip></el-table-column>
        <el-table-column prop="productname" label="产品名称" align="center" show-overflow-tooltip></el-table-column>
        <el-table-column
          prop="type"
          label="类型"
          align="center"
          width="80"
          :formatter="row => typeArr[row.type]"
        ></el-table-column>
        <el-table-column
          prop="isuse"
          label="状态"
          align="center"
          width="50"
          :formatter="row => (row.isuse ? '是':'否')"
        ></el-table-column>
        <el-table-column
          prop="moduletype"
          label="模块类型"
          align="center"
          width="80"
          :formatter="row => moduletypeObj[row.moduletype]"
        ></el-table-column>
        <el-table-column
          prop="source"
          label="导入来源"
          align="center"
          width="80"
          :formatter="row => sourceArr[row.source]"
        ></el-table-column>
        <el-table-column prop="description" label="样本范围" align="center" show-overflow-tooltip></el-table-column>
        <el-table-column
          prop="createtime"
          label="导入时间"
          align="center"
          width="180"
          :formatter="timeFormatter"
        ></el-table-column>
        <el-table-column label="操作" align="center" width="140">
          <template slot-scope="scope">
            <router-link class="btn-view" :to="{'path': `/sample-view/${scope.row.modulename}`}">查看</router-link>
            <router-link
              class="btn-view"
              :to="{'path': `/sample-edit/${scope.row.modulename}`}"
              style="margin-left: 15px;"
            >编辑</router-link>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        v-if="dataSource.length"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="page"
        :page-sizes="sizes"
        :page-size="size"
        layout="prev, pager, next, sizes"
        :total="total"
      ></el-pagination>
    </el-card>
  </div>
</template>

<script>
import dayjs from 'dayjs';
import {getSampleList, getAllModuletype} from '@/api/sample.js';
export default {
  name: 'page-samples',
  data() {
    return {
      page: 1,
      size: 10,
      total: 0,
      sizes: [10, 20, 50, 100],
      typeArr: ['', '样本', '决策模块'],
      moduletypeObj: {},
      sourceArr: ['', 'Excel', 'RC'],
      moduleName: '',
      headerStyle: {
        color: '#409eff'
      },
      dataSource: []
    };
  },
  created() {
    console.log('=======samp created=======');
    // 获取样本库列表
    getAllModuletype()
      .then(result => (this.moduletypeObj = result.result || {}))
      .catch(err => console.log(err));
    this.getDatasource(this.moduleName);
  },
  mounted() {
    console.log('=========samp mounted=========');
  },
  methods: {
    timeFormatter(item) {
      return dayjs(item.createtime).format('YYYY-MM-DD HH:MM:ss');
    },
    getDatasource(moduleName = '') {
      getSampleList(moduleName, this.page, this.size)
        .then(result => {
          const data = result.result;
          this.total = data.totalNum || 0;
          if (Array.isArray(data.items)) {
            this.dataSource = data.items;
            return;
          }
          this.dataSource = [];
        })
        .catch(err => console.log(err));
    },
    handleSizeChange(val) {
      this.size = val;
      this.page = 1;
      this.getDatasource(this.moduleName);
    },
    handleCurrentChange(val) {
      this.page = val;
      this.getDatasource(this.moduleName);
    },
    checkVariable(item) {
      this.$router.push({
        name: 'view',
        params: {
          id: item.id,
          name: item.modulename
        }
      });
    }
  }
};
</script>

<style scoped>
.el-card {
  margin: 20px 15px;
  padding-bottom: 20px;
}
.el-table {
  margin-top: 20px;
}
.el-input {
  float: right;
  width: 300px;
}
.el-card__header .el-button {
  float: right;
  margin-top: -11px;
}
</style>
